.overpassFix {
  transform: translateY(5px);

  &.heading1 {
  }

  &.heading2 {
    transform: translateY(4px);
  }

  &.heading3 {
    transform: translateY(3px);
  }

  &__heading1 {

    &_mobile {
      padding-top: 3px;
    }
  }

  &__heading3 {

    &_mobile {
      padding-top: 3px;
    }
  }

  &__heading4 {
    padding-top: 2px;
  }

  &UserName {
    transform: translateY(2px);
  }
}

@media (max-width: 960px) {
  .overpassFix {

    &.heading1 {
      transform: translateY(4px);
    }
  }
}

@media (max-width: 755px) {
  .overpassFix {

    &.heading1, &.heading2 {
      transform: translateY(3px);
    }
  }
}