.welcome-block {
  padding-top: 128px;
  padding-bottom: 111px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: url('../../../../Shared/images/bg/plain/2k.jpg') center 30% / cover no-repeat;

  &__container {
    width: 1140px;
  }

  &__content {
    width: 535px;

    &-text {
      color: #ffffff;
      font-family: "Source Sans Pro", sans-serif;
      font-size: 20px;
      font-weight: 400;
      line-height: 26px;
      margin-top: 23px;
    }

    &-button {
      margin-top: 34px;
    }

    .heading1 {
      color: #ffffff;
    }
  }
}

@media (max-width: 2048px) {
  .welcome-block {
    background: url('../../../../Shared/images/bg/plain/2k.jpg') 40% center / cover no-repeat;
  }
}

@media (max-width: 1170px) {
  .welcome-block {
    padding-bottom: 128px;
    background: url('../../../../Shared/images/bg/plain/desktop.jpg') center center / cover no-repeat;

    &__container {
      width: 929px;
    }

    &__content {
      width: 450px;

      &-button {
        margin-top: 40px;
      }
    }
  }
}

@media (max-width: 960px) {
  .welcome-block {
    padding-top: 109px;
    padding-bottom: 90px;
    background: url('../../../../Shared/images/bg/plain/tablet.jpg') center center / cover no-repeat;

    &__container {
      width: 721px;
    }

    &__content {
      width: 410px;

      &-text {
        font-size: 18px;
        line-height: 24px;
      }

      &-button {
        margin-top: 33px;
      }
    }
  }
}

@media (max-width: 755px) {
  .welcome-block {
    padding-top: 64px;
    padding-bottom: 40px;
    background: url('../../../../Shared/images/bg/plain/mobile.jpg') center center / cover no-repeat;

    &__container {
      width: 100vw;
      padding: 0 15px;
      box-sizing: border-box;
    }

    &__content {
      width: 100%;

      .heading1 {
        text-align: center;
        line-height: 34px;
      }

      &-text {
        font-size: 16px;
        line-height: 22px;
        text-align: center;
        margin-top: 13px;
      }

      &-button {
        margin: 38px auto 0 auto;
      }
    }
  }
}

@media (min-resolution: 192dpi) {
  .welcome-block {
    background: url('../../../../Shared/images/bg/retina/2k.jpg') center 30% / cover no-repeat;
  }
}

@media (max-width: 2048px) and (min-resolution: 192dpi) {
  .welcome-block {
    background: url('../../../../Shared/images/bg/retina/2k.jpg') 40% center / cover no-repeat;
  }
}

@media (max-width: 1170px) and (min-resolution: 192dpi) {
  .welcome-block {
    background: url('../../../../Shared/images/bg/retina/desktop.jpg') center center / cover no-repeat;
  }
}

@media (max-width: 960px) and (min-resolution: 192dpi) {
  .welcome-block {
    background: url('../../../../Shared/images/bg/retina/tablet.jpg') center center / cover no-repeat;
  }
}

@media (max-width: 755px) and (min-resolution: 192dpi) {
  .welcome-block {
    background: url('../../../../Shared/images/bg/retina/mobile.jpg') center center / cover no-repeat;
  }
}

@media (max-height: 750px) {
  .welcome-block {
    padding-top: 50px;
    padding-bottom: 30px;
  }
}