.bannerReg {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 130px;
  padding-bottom: 100px;

  &__container {
    width: 1140px;
    padding: 0 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    text-align: center;
  }

  &__content {
    margin-top: 43px;
    align-self: stretch;
    display: flex;

    &-text {
      width: 550px;
    }

    &-manLaptop {
      margin-left: 99px;
      margin-top: 4px;
    }
  }
}

@media (max-width: 1170px) {
  .bannerReg {
    padding-bottom: 151px;

    &__container {
      width: 929px;
    }

    &__content {

      &-text {
        width: 450px;
      }

      &-manLaptop {
        margin-left: 50px;
        margin-top: 16px;
      }
    }
  }
}

@media (max-width: 960px) {
  .bannerReg {
    padding-top: 110px;
    padding-bottom: 120px;

    &__title {
      width: 420px;
    }

    &__container {
      width: 721px;
    }

    &__content {
      margin-top: 40px;
      padding-left: 1px;
      justify-content: space-between;

      &-text {
        width: 340px;
      }

      &-manLaptop {
        margin-left: 0;
        margin-top: 4px;
      }
    }
  }
}

@media (max-width: 755px) {
  .bannerReg {
    padding-top: 63px;
    padding-bottom: 65px;

    &__container {
      width: 100vw;
      padding: 0 15px;
      box-sizing: border-box;
    }

    &__title {
      width: 260px;
    }

    &__content {
      flex-direction: column;
      align-items: center;
      margin-top: 25px;

      &-text {
        width: 290px;
        text-align: center;
        margin-top: 22px;
      }

      &-manLaptop {
        order: -1;
        margin-left: 0px;
        //transform: scale(0.65);
      }
    }
  }
}
